  box-sizing: border-box;
  outline: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Open sans", sans-serif;
  font-size: 1.3em;
  line-height: 1.5em;
  background-color: #E2E2E2;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-attachment: fixed;
  background-size: cover;
}

.nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 70px;
  background-color: rgba(51, 51, 51, 1);
  color: white;
}

.nav-container .menu,
.nav-container .list-main .list-content,
.item {
  color: white;
  text-decoration: none;
  list-style: none;
}

.nav-container .menu {
  font-weight: 700;
}

/*.nav-container .list-main .list-content .list-item .item {} */
.session-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 4rem auto;
  width: 50%;
  font-size: 2em;
}

.session-main h1 {
  text-decoration: solid underline;
  margin: 10px 0;
}

.session-main p {
  font-style: italic;
}

.session-main figure {
  margin: 0 auto;
}

.session-main figure img {
  width: 90%;
}

.formulario {
  width: 100%;
  margin-top: 2rem;
  padding: 40px;
  box-shadow: inset 0 0 5px 0 black;
}

.formulario form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.formulario form label {
  font-size: 2rem;
  width: 90%;
}

.formulario .form-inputs {
  width: 90%;
  padding: 15px;
  font-size: 1.5rem;
  border-radius: 30px;
}

.formulario div {
  width: 50%;
}

.formulario div button {
  margin-top: 25px;
  padding: 15px;
  width: 100%;
  font-size: 1.3rem;
  font-weight: 700;
  border-radius: 30px;
  cursor: pointer;
